import intro from '../../config/intro.json'
import profile from '../../config/profile.json'

interface ReturningUseConfig {
  profile: {
    author: string
    title: string
    titleTemplate: string
    placeholder: string
    siteUrl: string
    description: string
    github: string
  }
  intro: {
    config: {
      name: string
      subtitle: string
      bio: string[]
      github: string
      notion: string
    }
    apply: {
      title: string
      list: {
        description: string
        icon: string
      }[]
    }
    activities: {
      description: string
      bio: string[]
      list: {
        title: string
        descriptions: string[]
      }[]
    }
    faq: {
      description: string | null
      list: {
        question: string
        answer: string | null
        url: string | null
      }[]
    }
  }
}

export function useConfig(): ReturningUseConfig {
  return {
    profile: profile as ReturningUseConfig['profile'],
    intro: intro as ReturningUseConfig['intro']
  }
}
